import React, { useCallback, useReducer, useState } from 'react';
import { makeStyles, TextField, Typography } from '@material-ui/core';
import { writeGasto } from '../api/sheetsApi';
import SaveButton from '../components/SaveButton';
import UnloggedPage from './UnloggedPage';
import { isNullOrEmpty } from '../utils/utils';
import NumberFormatCustom from '../components/NumberFormatCustom';
import CategorySelect from '../components/CategorySelect';
import { useAuthContext } from '../context/AuthContext';

const useStyles = makeStyles((theme) => ({
    input: {
        margin: theme.spacing(3),
    },
    buttonContainer: {
        display: 'flex',
        justifyContent: 'center',
    },
    form: {
        // display: 'flex',
        // flexDirection: 'column',
    },
    error: {
        color: theme.palette.error.main,
        textAlign: 'center',
        marginBottom: theme.spacing(1),
    },
}));

interface FormField {
    value: string,
    error: boolean,
}

interface Form {
    [index: string]: FormField,
    description: FormField,
    category: FormField,
    amount: FormField,
}

interface FormAction {
    action: 'reset' | 'update' | 'validate',
    name?: string,
    value?: string | boolean,
}

const initialValues: Form = {
    description: { value: '', error: false },
    category: { value: '', error: false },
    amount: { value: '', error: false },
};

function fieldsReducer(previous: Form, action: FormAction): Form {
    if (action.action === 'reset') {
        return initialValues;
    }

    if (action.name !== undefined) {
        if (action.action === 'update' && typeof action.value === 'string') {
            return {
                ...previous,
                [action.name]: {
                    ...previous[action.name],
                    value: action.value,
                },
            };
        }

        if (action.action === 'validate' && typeof action.value === 'boolean') {
            return {
                ...previous,
                [action.name]: {
                    ...previous[action.name],
                    error: action.value,
                },
            };
        }
    }

    return previous;
}

const MainPage: React.VFC = () => {
    const classes = useStyles();
    const [fields, updateFields] = useReducer(fieldsReducer, initialValues);
    const [promise, setPromise] = useState<Promise<unknown>>();
    const [errorMessage, setErrorMessage] = useState('');
    const auth = useAuthContext();

    const handleFieldChange = useCallback(({ target: { name, value } }) => {
        updateFields({ action: 'update', name: name, value: value });
        updateFields({ action: 'validate', name: name, value: false });
    }, [updateFields]);

    const handleSaveClick = useCallback(() => {
        let formHasErrors = false;
        Object.keys(fields).forEach((fieldName: string) => {
            const error = isNullOrEmpty(fields[fieldName].value);

            if (error) {
                formHasErrors = true;
            }

            updateFields({
                action: 'validate',
                name: fieldName,
                value: error,
            });
        });

        if (formHasErrors) {
            setPromise(new Promise((resolve, reject) => {
                reject();
            }));
        }
        else {
            const writePromise = writeGasto(
                fields.description.value,
                fields.category.value,
                fields.amount.value,
            );
            writePromise.then(() => {
                updateFields({ action: 'reset' });
            }).catch(() => {
                setErrorMessage('Hubo un problema guardando los datos.');
            });
            setPromise(writePromise);
        }
    }, [fields]);

    const handleSignIn = useCallback(() => {
        auth.setIsSignedIn(true);
    }, [auth]);

    if (!auth.isSignedIn) {
        return (<UnloggedPage onSignedIn={handleSignIn} />);
    }

    return (
        <div>
            <form className={classes.form}>
                <div className={classes.input}>
                    <TextField
                        label="Descripción"
                        name="description"
                        fullWidth
                        value={fields.description.value}
                        onChange={handleFieldChange}
                        error={fields.description.error}
                    />
                </div>
                <div className={classes.input}>
                    <CategorySelect
                        label="Categoría"
                        name="category"
                        fullWidth
                        value={fields.category.value}
                        onChange={handleFieldChange}
                        error={fields.category.error}
                    />
                </div>
                <div className={classes.input}>
                    <TextField
                        label="Cantidad"
                        name="amount"
                        fullWidth
                        value={fields.amount.value}
                        onChange={handleFieldChange}
                        error={fields.amount.error}
                        InputProps={{
                            inputComponent: NumberFormatCustom,
                        }}
                    />
                </div>
            </form>
            <Typography
                className={classes.error}
                component="div"
                variant="body2"
            >
                {errorMessage}
            </Typography>
            <SaveButton
                onClick={handleSaveClick}
                promise={promise}
            />
        </div>
    );
};

export default MainPage;
