import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';

const NotFoundPage: React.VFC = () => {
    const history = useHistory();

    useEffect(() => {
        setTimeout(() => {
            history.push('/');
        }, 3000);
    }, [history]);

    return (
        <div>
            404 - Página no encontrada
            Redirigiendo al inicio...
        </div>
    );
};

export default NotFoundPage;
