import React, { FC } from 'react';
import { FormControl, InputLabel, MenuItem, Select, SelectProps } from '@material-ui/core';

const categories = [
    'Casa',
    'Supermercado',
    'Farmacia',
    'Electrónica',
    'Gustos',
    'Auto',
    'Streaming',
    'Otros',
    'Maru',
].sort();

const CategorySelect: FC<SelectProps> = ({ fullWidth, ...props }) => {
    return (
        <FormControl fullWidth={fullWidth}>
            <InputLabel id="category-select-label">
                Categoría
            </InputLabel>
            <Select
                labelId="category-select-label"
                label="Categoría"
                {...props}
            >
                {categories.map((category) => (
                    <MenuItem
                        key={`itCat${category}`}
                        value={category}
                    >
                        {category}
                    </MenuItem>
                ))}
            </Select>
        </FormControl>
    );
};

export default CategorySelect;
