import { isNullOrEmpty } from '../utils/utils';
import { getUser } from './authApi';
import { spreadsheetId } from './credentials';

export async function getGastos() {
    try {
        const response = await window.gapi.client.sheets.spreadsheets.values.get({
            spreadsheetId: spreadsheetId,
            range: 'Gastos!A2:B',
        });
        const range = response.result;
        let out = 'Apellido, Nombre:';
        if (range.values && range.values.length > 0) {
            for (let i = 0; i < range.values.length; i++) {
                const row = range.values[i];
                out += `${row[1]}, ${row[0]}`;
            }
        }
        else {
            out = 'No data found.';
        }
        return out;
    }
    catch {
        throw new Error('Error haciendo spreadsheets.values.get');
    }
}

export async function writeGasto(description: string, category: string, amount: string) {
    const errors = [];
    if (isNullOrEmpty(description)) {
        errors.push('descripción');
    }
    if (isNullOrEmpty(category)) {
        errors.push('categoría');
    }
    if (isNullOrEmpty(amount)) {
        errors.push('cantidad');
    }

    if (errors.length > 0) {
        const verb = errors.length === 1 ? 'tiene' : 'tienen';
        let fields = `${errors.join(', ')}`;
        fields = fields.slice(0, 1).toLocaleUpperCase().concat(fields.slice(1));
        throw new Error(`${fields} ${verb} error.`);
    }

    try {
        const date = new Date().toLocaleString('es-AR', {
            year: 'numeric',
            month: '2-digit',
            day: '2-digit',
        });

        const user = getUser();
        let name = '';
        if (user === 'Fernando') {
            name = 'Fer';
        }
        else if (user === 'Mariela') {
            name = 'Maru';
        }
        else {
            name = user;
        }

        await window.gapi.client.sheets.spreadsheets.values.append({
            spreadsheetId: '1IP_zrL81Ej2TCLD5qIYWdP8fFk3xYYvJ_-kogZWAa9E',
            range: 'Gastos!A2:E',
            valueInputOption: 'USER_ENTERED',
            resource: {
                values: [[description, category, amount, date, name]],
            },
        });
    }
    catch (error) {
        throw new Error(error);
    }
}
