import React, { useCallback, useEffect, useState } from 'react';
import { Button, makeStyles, Typography } from '@material-ui/core';
import { signIn } from '../api/authApi';
import { useAuthContext } from '../context/AuthContext';

const useStyles = makeStyles((theme) => ({
    root: {
        margin: theme.spacing(3),
        textAlign: 'center',
    },
    spacing: {
        marginBottom: theme.spacing(2),
    },
    button: {
        marginTop: theme.spacing(2),
    },
    errorMessage: {
        color: theme.palette.error.main,
    },
}));

interface UnloggedPageProps {
    onSignedIn?(): void,
}

const UnloggedPage: React.VFC<UnloggedPageProps> = ({ onSignedIn }) => {
    const classes = useStyles();
    const auth = useAuthContext();
    const [errorMessage, setErrorMessage] = useState('');

    const handleSignInClick = useCallback(() => {
        auth.signIn().then(() => {
            if (onSignedIn) {
                onSignedIn();
            }
        });
    }, [auth, onSignedIn]);

    useEffect(() => {
        setErrorMessage(auth.error ?? '');
    }, [auth.error]);

    return (
        <div className={classes.root}>
            <Typography className={classes.spacing} variant="h4">
                Oops! :(
            </Typography>
            <Typography className={classes.spacing} variant="body1">
                Tenés que loguearte para poder usar la app
            </Typography>
            <Typography className={classes.errorMessage} variant="body1">
                {errorMessage}
            </Typography>
            <Button
                className={classes.button}
                color="primary"
                onClick={handleSignInClick}
            >
                Loguearme con Google
            </Button>
        </div>
    );
};

export default UnloggedPage;
