import { apiKey, clientId, discoveryDocs, scopes } from './credentials';

type PromiseFunction = (value?: unknown) => void;
type SignInCallback = (value: boolean) => void;

/**
 *  Initializes the API client library and sets up sign-in state listeners.
 */
export function initClient(resolve: PromiseFunction, reject: PromiseFunction) {
    return (signInCallback: SignInCallback) => {
        window.gapi.client.init({
            apiKey: apiKey,
            clientId: clientId,
            discoveryDocs: discoveryDocs,
            scope: scopes,
        }).then(() => {
            // Listen for sign-in state changes.
            window.gapi.auth2.getAuthInstance().isSignedIn.listen(signInCallback);

            // Handle the initial sign-in state.
            if (signInCallback) {
                signInCallback(window.gapi.auth2.getAuthInstance().isSignedIn.get());
            }

            resolve();
        }, (error: Record<string, unknown>) => {
            reject(error);
        });
    };
}

export function isSignedIn(): boolean {
    return window.gapi.auth2.getAuthInstance().isSignedIn.get();
}

export function signIn() {
    return window.gapi.auth2.getAuthInstance().signIn();
}

export function signOut() {
    return window.gapi.auth2.getAuthInstance().signOut();
}

export function getUser() {
    return window.gapi.auth2?.getAuthInstance()?.currentUser.get()?.getBasicProfile()?.getGivenName();
}

/**
 *  On load, called to load the auth2 library and API client library.
 */
export function gapiLoad() {
    return new Promise((resolve, reject) => {
        window.gapi.load('client:auth2', initClient(resolve, reject));
    });
}
