import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { gapiLoad, isSignedIn as gapiIsSignedIn, signIn, signOut } from '../api/authApi';

interface AuthContextProps {
    isSignedIn: boolean,
    setIsSignedIn: React.Dispatch<boolean>,
    error: string | undefined,
    signIn(): Promise<unknown>,
    signOut(): Promise<unknown>,
}

const AuthContext = React.createContext<AuthContextProps | undefined>(undefined);
AuthContext.displayName = 'AuthContext';

export const AuthContextProvider: React.FC = ({ children }) => {
    const [isSignedIn, setIsSignedIn] = useState<boolean>(false);
    const [error, setError] = useState<string | undefined>(undefined);

    const handleSignIn = useCallback(() => {
        const errorLogueo = 'Hubo un error con el logueo. Estos programadores son todos iguales. Preguntale a Fer si lo puede arreglar.';
        return signIn().then(() => {
            setIsSignedIn(true);
        }, () => {
            setError(errorLogueo);
        }).catch(() => {
            setError(errorLogueo);
        });
    }, []);

    const handleSignOut = useCallback(() => {
        return signOut().then(() => {
            setIsSignedIn(false);
        });
    }, []);

    useEffect(() => {
        gapiLoad().then(() => {
            setIsSignedIn(gapiIsSignedIn());
        }).catch((ex) => {
            if (ex.error === 'idpiframe_initialization_failed') {
                setError(`Tu URL no está admitida por Google. URL: ${window.location.href}`);
            }
            else {
                setError(JSON.stringify(ex));
            }
        });
    }, []);

    const value = useMemo(() => {
        return {
            isSignedIn: isSignedIn,
            setIsSignedIn: setIsSignedIn,
            error: error,
            signIn: handleSignIn,
            signOut: handleSignOut,
        };
    }, [error, handleSignIn, handleSignOut, isSignedIn]);

    return (
        <AuthContext.Provider value={value}>
            {children}
        </AuthContext.Provider>
    );
};

export function useAuthContext(): AuthContextProps {
    const context = React.useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuthContext tiene que estar dentro de un AuthContextProvider');
    }
    return context;
}
